.text__wrapper {
    width: calc(100%);
    display: grid;
    grid-template-areas: ". text .";
    padding-top: 5vh;
}

.text {
    color: grey;
    grid-area: text;
    /* height: 571px; */
    aspect-ratio: 1.6;
    font-size: 30px;
    resize: none;
    background-color: rgba(0, 0, 45, 0.4);
    backdrop-filter: blur(2px);
    border-radius: 10px;
    box-shadow: 0px 0px 36px -14px rgba(255, 255, 255, 0.75);
    -webkit-box-shadow: 0px 0px 36px -14px rgba(255, 255, 255, 0.75);
    -moz-box-shadow: 0px 0px 36px -14px rgba(255, 255, 255, 0.75);
}
