.step{
    display: flex;
    align-items: center;
    flex-direction: column;
    font-family: poppins;
}

.step button{
    padding: 0 2em;
    border-radius: 10px;
    border: none;
    color: white;
    transition: 0.2s ease-in-out all;
    font-family: poppins;
}

button[color="red"]{
    background-color: red;
}

button[color="blue"]{
    background-color: blue;
}

.step button:hover{
    cursor: pointer;
}