.font{
    width: 40%;
    margin: 2em auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}

.font h1{
    grid-column: 1/2;
}

select{
    grid-column: 2/4;
    background-color: transparent;
    color: white;
    font-family: poppins;
    border: 0px solid black;
    font-size: 2em;
    padding: 0 0 0 1em;
}

option{
    color: black;
    padding: 0;
}