.Nav {
    /* padding: 1em; */
    width: 100%;
    display: grid;
    grid-template-areas: ". . . . title title title title . . . .";
    border-bottom: 1px solid white;
    /* place-items: center; */
}

.Nav__title {
    grid-area: title;
    display: grid;
    place-items: center;
    font-size: 3em;
    font-family: "Poppins", sans-serif;
    /* letter-spacing: 5px; */
    /* font-family: "Five Years Later", montserrat; */
    /* font-weight: bold; */
}


button.Nav__theme {
    grid-area: theme;
}
