.Result {
    width: 809px;
    position: relative;
    margin: 3em auto 0 auto;
}

.Result img {
    width: 100%;
}

#Result__text {
    top: 120px;
    left: 130px;
    font-size: 25px;
    width: 679px;
    position: absolute;
    line-height: 30px;
    vertical-align: bottom;
    height: 963px;
    overflow: hidden;

    overflow-wrap: break-word;
    word-wrap: break-word;

    -ms-word-break: break-all;

    -ms-hyphens: auto;
    -moz-hyphens: auto;
    -webkit-hyphens: auto;
    hyphens: auto;
    color: #000f55;
}
